/*Carousel CSS*/
.carousel{
    position:relative;
    height:calc(50% + 10px);
    width:35%;
    min-width:600px;
    margin: 0 auto;
  }
  
  .carousel__image {
    height:100%;
    max-width:100%;
  
  }
  
  .carousel__track-container {
    height:100%;
    width:100%;
    background:rgba(0,0,0,.2);
    padding-top:2.5%;
    padding-bottom:2.5%;
    position:relative;
    overflow:hidden;
  }
  
  .carousel__track {
    padding: 0;
    margin: 0;
    list-style: none;
    position: relative;
    height:100%;
    transition: -webkit-transform 250ms ease-in;
    transition: transform 250ms ease-in;
    transition: transform 250ms ease-in, -webkit-transform 250ms ease-in;
  }
  
  .carousel__slide {
    position: absolute;
    text-align: center;
    top:0;
    bottom:0;
    width:100%;
    min-width:600px;
  
  
  }
  .carousel__button {
    position:absolute;
    top:50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background: transparent;
    border:0;
    cursor:pointer;
  }
  .carousel__button--left{
    left:-40px;
  }
  .carousel__button--right{
    right:-40px;
  }
  .carousel__button img {
    width:24px;
  }
  .carousel__button:hover {
      opacity:0.7;
  }
  .carousel__nav{
    background:transparent;
    justify-content:center;
    display: flex;
    padding-top: 3px;
  }
  .carousel__indicator {
    border:0;
    border-radius:50%;
    width:12;
    height:12px;
    background:rgba(0,0,0,.3);
    margin: 0 10px;
    cursor:pointer;
  }
  
  .carousel__indicator.current-slide {
    background: rgba(0,0,0,.75);
  }
  .carousel__indicator:hover {
    background:rgba(0,0,0,.5);
  }
  .is-hidden { display:none}
